









































































import {Component, Vue} from 'vue-property-decorator';

@Component
export default class DashBoard extends Vue {

    navigatorList: any = [
        {
            name: '仪表盘',
            isSelect: true,
            path: 'dashBoard'
        }, {
            name: '市场',
            isSelect: false,
            path: 'market'
        }, {
            name: '转账',
            isSelect: false,
            path: 'transfer'
        }, {
            name: '收据',
            isSelect: false,
            path: 'receipt'
        }, {
            name: '委托收益',
            isSelect: false,
            path: 'remote'
        }]
    mosaicList = [
        {
            name: 'XEM',
            amount: 0.265874,
            rate: 30.55
        },
        {
            name: 'ETC',
            amount: 0.265874,
            rate: 30.55
        },
        {
            name: 'ETH',
            amount: 0.265874,
            rate: 30.55
        },
        {
            name: 'BTC',
            amount: 0.265874,
            rate: 30.55
        }
    ]
    isShowAccountInfo = true;
    isShowManageMosaicIcon = false

    switchPanel(index) {
        const list = this.navigatorList.map((item) => {
            item.isSelect = false
            return item
        });
        list[index].isSelect = true
        this.navigatorList = list
        this.$router.push({
            name: list[index].path
        })
    }

    hideAssetInfo() {
        this.isShowAccountInfo = false;
    }

    manageMosaicList() {
        this.isShowManageMosaicIcon = !this.isShowManageMosaicIcon
    }

    deleteMosaic(index) {
        this.mosaicList.splice(index, 1)
    }

    created() {
        this.$router.push({
            name: 'dashBoard'
        })
    }
}
